.showcase {
    display: grid;
    grid-template-columns: 20% 80%;
    grid-column-gap: 2.5rem;
    margin: 7.5rem auto;
    padding: 1.5rem 5.5rem;
    /* border: 2px solid red; */
}

.showcase-text {
    /* border: 2px solid red; */
    line-height: 6rem;
    font-size: 3.25rem;
    text-align: center;
    margin: auto auto;
    font-family: 'Caveat', cursive;
    color:#ffff;
    /* text-shadow: 1px 3px 0 #969696, 1px 13px 5px #aba8a8; */
    text-shadow: -4px 6px 3px rgb(236, 149, 158);

}

.showcase-text h2 {
    font-size: 4.25rem;
}


.showcase-img img {
  width: 100%;
  border: transparent;
  -webkit-clip-path: circle(41.9% at 53% 50%);
          clip-path: circle(41.9% at 53% 50%);
  box-shadow: 0px 0px 152px 1px rgba(0,0,0,0.7);
}


/* Media Queries */
@media only screen and (max-width: 1200px) {
  .showcase-text{
      line-height: 4.5rem;
      font-size: 2.5rem;
  }
  .showcase-text h2 {
    font-size: 3.5rem;
  }
}
@media only screen and (max-width: 800px) {
  .showcase {
      display: block;
  }
  .showcase-text{
      line-height: 3.5rem;
      font-size: 1.75rem;
  }
  .showcase-text h2 {
    font-size: 2.5rem;
  }
  .showcase-img {
      text-align: center;
  }
}
@media only screen and (max-width: 500px) {
  .showcase-text{
      width: 100%;
      line-height: 2.5rem;
      font-size: 1.5rem;
  }
  .showcase-text h2 {
    margin-top: 1.25rem;
    font-size: 2rem;
  }
  .showcase-img {
      margin-bottom: 1rem;
  }
}

/* @import url("https://fonts.googleapis.com/css2?family=Reenie+Beanie&display=swap"); */

#about {
  margin: 4.5rem auto;
  margin-top: 12.5rem;
  padding: 2.5rem;
  /* border: 2px solid red; */
}

ul,
li {
  list-style: none;
  margin: auto auto;
}

ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: auto auto;
}

ul li a {
  text-decoration: none;
  color: #000;
  background: #ffc;
  display: block;
  /* font-family: "Reenie Beanie", cursive; */
  font-family: 'Caveat', cursive;
  /* height: 15rem; */
  width: 20rem;
  padding: 1.75rem;
  box-shadow: 0px 0px 80px 1px rgba(0, 0, 0, 0.7);
  transition: transform 0.15s linear;
  transform: rotate(-6deg);
}

ul li {
  margin: 3.5rem;
}

ul li:nth-child(even) a {
  transform: rotate(4deg);
  background: #cfc;
  position: relative;
  top: 5px;
}
ul li:nth-child(3n) a {
  transform: rotate(-3deg);
  background: #ccf;
  position: relative;
  top: -5px;
}
ul li:nth-child(5n) a {
  transform: rotate(5deg);
  position: relative;
  top: -10px;
}

ul li a:hover,
ul li a:focus {
  /* box-shadow:10px 10px 7px rgba(0,0,0,.7); */
  transform: scale(1.25);
  position: relative;
  z-index: 5;
}

h2 {
  font-weight: bold;
  font-size: 2.5rem;
  text-align: center;
  margin-bottom: 0.75rem;
}

p {
  font-size: 2rem;
  font-weight: normal;
}

#card-list {
  display: block;
}

#card-list li {
  list-style:disc;
  margin: 0.15rem 1.75rem;
  font-size: 2rem;
}
#skills-section {
  /* border: 2px solid red; */
  padding: 2.5rem;
}

#skills-section h1 {
  text-align: center;
  font-size: 5.5rem;
  font-family: "Caveat", cursive;
  color:#ffff;
  text-shadow: -4px 6px 3px rgb(236, 149, 158);
}

#skills {
  /* border: 2px solid red; */
  margin: auto auto;
  padding: 3.5rem;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 1.5rem;
}

.icon-card {
  border: none;
  border-color: transparent;
  padding: 0;
  text-align: center;
  background-color: #3c34343b;
  transition: all ease-in 0.3s;
  /* border: 2px solid red; */
}

.icon-card img {
  /* border: 2px solid red; */
  width: 100px;
  margin: auto auto;
  padding: 0;
}

.icon-card:hover {
  cursor: pointer;
  background-color: black;
  transform: scale(1.1);
}

@media only screen and (max-width: 800px){
    
  #skills {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media only screen and (max-width: 500px){
    
  #skills {
    grid-template-columns: repeat(1, 1fr);
  }
}

.container {
  /* border: 2px solid red; */
  padding: 2rem;
  margin: 3.5rem auto;
  max-width: 800px;
}

.top {
  /* border: 2px solid red; */
  text-align: center;
  padding: 4rem 0;
  max-width: 600px;
  margin: 0 auto;
}

.top h1 {
  font-size: 5.5rem;
  font-family: "Caveat", cursive;
  color: #ffff;
  text-shadow: -4px 6px 3px rgb(236, 149, 158);
}

.top p {
    font-family: "Josefin Sans", sans-serif;
    font-size: 2.5rem;
    font-weight: bolder;

}

.timeline {
  position: relative;
  min-height: 150px;
  /* border: 2px solid blue;  */
}

.line {
  position: absolute;
  z-index: 2;
  left: calc(50% - 1px);
  width: 2px;
  top: -50px;
  bottom: -50px;
  background-color: #000;
}

.line::before,
.line::after {
  position: absolute;
  display: block;
  content: "";
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  background-color: #000;
  left: 50%;
  transform: translateX(-50%);
}

.line::after {
  bottom: 0;
}

.section {
  width: 100%;
  display: flex;
  flex-direction: row;
  opacity: 0;
  transform: translateX(20%);
  transition: all ease 600ms;
  position: relative;
  z-index: 1;
  margin: 50px 0;
  padding: 1rem;
  border-radius: 1rem;
  align-items: center;
  min-height: 300px;
}

.section:nth-child(odd) {
  flex-direction: row-reverse;
  transform: translateX(-20%);
}

.bead {
  position: absolute;
  display: block;
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  background-color: #000;
  left: 50%;
  top: 20%;
  transform: translateX(-50%);
}

.content {
  width: calc(50% - 2rem);
  padding: 1.25rem 2rem;
  border: none;
  border-color: transparent;
  background-color: #3c34343b;
  font-family: "Josefin Sans", sans-serif;
  transition: all ease 600ms;
}

.content:hover {
  background-color: #000;
  color: white;
  cursor: pointer;
}

.content p {
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.content h2 {
  font-size: 2rem;
  font-weight: bolder;
}

.content a {
  padding: 0.3rem 0.5rem;
  text-decoration: none;
  margin-right: 1.25rem;
  font-size: 1.2rem;
  border: none;
  border-color: transparent;
  border-radius: 1rem;
  background-color: #e08292;
  color: white;
}

.show-me:nth-child(n) {
  transform: none;
  opacity: 1;
}

h1 {
  font-weight: 400;
  letter-spacing: 1px;
}


@media only screen and (max-width: 700px) {
  .line,.line::after,.line::before {
    background: none;
  }

  .bead {
    display: none;
  }

  .content {
    width: 100%;
  }
}
